import React, { createContext, useMemo, useState } from 'react';
import { createPortal } from 'react-dom';
import { v4 as uuidv4 } from 'uuid';

import { Toast } from './Toast';
import {
  AppendToastHandler,
  RemoveToastHandler,
  ToastList,
} from './toastTypes';

type ToastContextValue = {
  toastList: ToastList;
  appendToast: AppendToastHandler;
};

const initialValue: ToastContextValue = {
  toastList: [],
  appendToast: () => {},
};

export const ToastContext =
  createContext<ToastContextValue>(initialValue);

interface ToastProviderProps {
  children: React.ReactNode;
}

export const ToastProvider = (props: ToastProviderProps) => {
  const { children } = props;
  const [toastList, setToastList] = useState<ToastList>([]);

  const appendToast: AppendToastHandler = (payload) => {
    const newToastValue = { ...payload, id: uuidv4() };
    setToastList((prev) => [...prev, newToastValue]);
  };

  const removeToast: RemoveToastHandler = (toastId) => {
    setToastList((prev) =>
      prev.filter((toast) => toast.id !== toastId),
    );
  };

  const value = useMemo(() => {
    return {
      toastList,
      appendToast,
    };
  }, [toastList]);

  return (
    <ToastContext.Provider value={value}>
      {children}

      {createPortal(
        <div className="toasts-wrapper">
          {toastList.map((toast) => (
            <Toast key={toast.id} onClose={() => removeToast(toast.id)}>
              {toast.message}
            </Toast>
          ))}
        </div>,
        document.body,
      )}
    </ToastContext.Provider>
  );
};
