/**
 * External imports
 */
import React from 'react';
import { Routes, Route } from 'react-router-dom';
import loadable from '@loadable/component';

/**
 * Internal imports
 */
import { GlobalStyles } from '~/libs/styles';
import { Layout } from '~/components/base';
import { Core } from './components/base/Core';

/**
 * Dynamic page imports
 */
const HomePage = loadable(() => import('~/pages/HomePage'));
const TodoCreatePage = loadable(
  () => import('~/pages/todo/TodoCreatePage'),
);
const TodoModifyPage = loadable(
  () => import('~/pages/todo/TodoModifyPage'),
);
const NotFoundPage = loadable(() => import('~/pages/NotFoundPage'));

const App = () => {
  return (
    <Core>
      <GlobalStyles />
      <Layout>
        <Routes>
          <Route path="/" element={<HomePage />} />
          <Route path="/todo">
            <Route path="create" element={<TodoCreatePage />} />
            <Route path=":todoId" element={<TodoModifyPage />} />
          </Route>
          <Route path="*" element={<NotFoundPage />} />
        </Routes>
      </Layout>
    </Core>
  );
};

export default App;
